import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';

function Square(props) {
  return (
    <button className="square"  onClick={props.onClick}  style={{color: props.squarecolor}}>
      {props.value}
    </button>
  );
}

class Board extends React.Component {
  renderSquare(i) {
    return (
      <Square
        value={this.props.squares[i]}
        onClick={() => this.props.onClick(i)}
        squarecolor = {this.props.styles[i]}
      />
    );
  }

  render() {
  //   return (
  //     <div>
  //       <div className="board-row">
  //         {this.renderSquare(0)}
  //         {this.renderSquare(1)}
  //         {this.renderSquare(2)}
  //       </div>
  //       <div className="board-row">
  //         {this.renderSquare(3)}
  //         {this.renderSquare(4)}
  //         {this.renderSquare(5)}
  //       </div>
  //       <div className="board-row">
  //         {this.renderSquare(6)}
  //         {this.renderSquare(7)}
  //         {this.renderSquare(8)}
  //       </div>
  //     </div>
  //   );
  // }
  let row = [], boardRow =[];
    for(let i=0; i<3; i++) {
      for (let j=0; j<3; j++) {
        row.push(this.renderSquare(j + i * 3));
      };
      boardRow.push(<div key={i} className="board-row">{row}</div>);
      row = [];
    }
    const board = <div> {boardRow} </div>;
   
     
    return board;
  }
}

class Game extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      history: [
        {
          squares: Array(9).fill(null)
        }
      ],
      stepNumber: 0,
      xIsNext: true,
      styles: Array(9).fill(null),
      isDesc: true
    };
  }
  handleClick(i) {
    const history = this.state.history.slice(0, this.state.stepNumber + 1);
    const current = history[history.length - 1];
    const squares = current.squares.slice();
    if (calculateWinner(squares) || squares[i] || check(squares)===9) {
      return;
    }
    squares[i] = this.state.xIsNext ? "X" : "O";
    this.setState({
      history: history.concat([
        {
          squares: squares
        }
      ]),
      stepNumber: history.length,
      xIsNext: !this.state.xIsNext
    });
  }
changeColor(squares)
{
  const s = this.state.styles.slice();
  const [a,b,c]= getPositionColor(squares);
  s[a]='red';
  s[b]='red';
  s[c]='red';
  this.state.styles = s;
}
toggle() {
    this.setState({
      isDesc: !this.state.isDesc
    })
  }
  jumpTo(step) {
    this.setState({
      stepNumber: step,
      xIsNext: (step % 2) === 0
    });
  }

  render() {
    const history = this.state.history;
    const current = history[this.state.stepNumber];
    const winner = calculateWinner(current.squares);
    const KT = check(current.squares);
    if(winner)
    {
      this.changeColor(current.squares);
    }
    else
    {
      this.state.styles = Array(9).fill(null);
    }
    const moves = history.map((step, move) => {
      const temp =  history[move];
      const locationX = getLocation(temp.squares,'X');
      const locationO = getLocation(temp.squares,'O');
      const desc = move ?
        'Go to move #' + move :
        'Go to game start';
      if(move === this.state.stepNumber){return (
        <li key={move}>
          <button onClick={() => this.jumpTo(move)} style={{color: 'red'}} >{desc}</button>
          <div>X:{locationX.join()}</div>
          <div>O:{locationO.join()}</div>
        </li>
      );}
      else
      {
        return (
          <li key={move}>
            <button onClick={() => this.jumpTo(move)}  >{desc}</button>
            <div>X:{locationX.join()}</div>
            <div>O:{locationO.join()}</div>
          </li>
        );
      }
    });

    let status;
    if (winner) {
      status = "Winner: " + winner;
    }
    else if(KT === 9)
    {
      status = "Hoa";
    } 
    else {
      status = "Next player: " + (this.state.xIsNext ? "X" : "O");
    }
    if(!this.state.isDesc) moves.reverse();
    return (
      <div className="game">
        <div className="game-board">
          <Board
            styles={this.state.styles}
            squares={current.squares}
            onClick={i => this.handleClick(i)}
          />
        </div>
        <div className="game-info">
          <div>{status}</div>
          <button onClick={() => this.toggle()}>sort</button>
          <ol>{moves}</ol>
        </div>
      </div>
    );
  }
}

// ========================================

ReactDOM.render(<Game />, document.getElementById("root"));

function calculateWinner(squares) {
  const lines = [
    [0, 1, 2],
    [3, 4, 5],
    [6, 7, 8],
    [0, 3, 6],
    [1, 4, 7],
    [2, 5, 8],
    [0, 4, 8],
    [2, 4, 6]
  ];
  for (let i = 0; i < lines.length; i++) {
    const [a, b, c] = lines[i];
    if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
      return squares[a];
    }
  }
  return null;
}
function check(squares)
{
  var dem = 0;
  for(let i = 0;i < squares.length;i++)
  {
      if(squares[i] !== null)
      {
        dem++;
      }
  }
  return dem;
}
function getPositionColor(squares){
  const lines=[
    [0,1,2], [3,4,5], [6,7,8], [0,3,6],
    [1,4,7], [2,5,8], [0,4,8], [2,4,6],
  ]
  for(let i = 0;i<lines.length; i++){
    const [a,b,c] = lines[i];
    if(squares[a] && squares[a] === squares[b] && squares[a] === squares[c]){
      return [a,b,c];
    }
  }
  return null;
}
function getLocation(squares,isNext)
{
  var temp = [];
  for(let i = 0;i < squares.length;i++)
  {
      if(squares[i] === isNext)
      {
        temp.push(i);
      }
  }
  return temp;
}



